import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowsAlt, faCompressArrowsAlt } from '@fortawesome/free-solid-svg-icons'

var myMarked = require('marked')

myMarked.setOptions({
  renderer: new myMarked.Renderer(),
  pedantic: false,
  gfm: true,
  tables: true,
  breaks: true,
  sanitize: false,
  smartLists: true,
  smartypants: false,
  xhtml: false
});


export default class OutputBox extends Component {
  state = {
    toggle: false,
    icon: faArrowsAlt
  }
  
  componentDidMount() {
    document.querySelectorAll('a').forEach(function(test) {
      test.setAttribute('target', '_blank');
    });
  }

  componentDidUpdate() {
    document.querySelectorAll('a').forEach(function(test) {
      test.setAttribute('target', '_blank');
    });
  }

  toggle = () => {
    if (this.state.toggle === false) {
      this.setState({
        toggle: !this.state.toggle,
        icon: faCompressArrowsAlt
      })
      document.getElementById('input').style.display = 'none';
      document.getElementById('preview').style.minHeight = '90vh';
      document.getElementById('preview').style.minWidth = '90vw';
      document.getElementById('preview').style.maxWidth = '100%';
      document.body.classList.toggle('toggled');
    }
    else {
      this.setState({
        toggle: !this.state.toggle,
        icon: faArrowsAlt
      })
      document.getElementById('input').style.display = 'block';
      document.getElementById('preview').style.minHeight = '40vh';
      document.getElementById('preview').style.minWidth = '75vw';
      document.getElementById('preview').style.maxWidth = '90%';
      document.body.classList.toggle('toggled');
    }
    return console.log(this.state.toggle)
  }

  createMarkup = () => {
    return {__html: `<div class="test">${myMarked(this.props.inputText())}</div>`}
}


  render() {
    return (
      <div id="preview" style={container}>
        <div id="header">
          <h3>Previewer</h3>
          <FontAwesomeIcon onClick={this.toggle} id="icon" icon={this.state.icon} />
        </div>
        <div id="markdowned" targert="_blank" style={output} dangerouslySetInnerHTML={this.createMarkup()}/>
      </div>
    )
  }
}

const container = {
    background: 'lightblue',
    minHeight: '40vh',
    minWidth: '75vw',
    maxWidth: '90%'

}
const output = {
  display: 'flex',
  paddingLeft: '10px',
  paddingRight: '10px',
  minHeight: '40vh',
  minWidth: '75vw',
  wordBreak: 'break-all'
}

OutputBox.propTypes = {
  inputText: PropTypes.func.isRequired
}